* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --dark-bg: #0d1117;
  --white: #ffffff;
  --gray: #40464d;
  --dark-gray: #2a2d30;
  --deep-dark: #161b22;
  --purple: #d2a8ff;
  --green: #7ee787;
}

html {
  display: flex;
  height: 100vh;
  font-size: 16px;
  color: var(--purple);
  font-family: monospace;
  background-color: var(--dark-bg);
  align-items: center;
  justify-content: center;
}

div {
  display: flex;
  flex-direction: column;
  width: 320px;
  word-wrap: break-word;
}

span, a {
  padding: 3px 0;
}
